.header{
    height: 55px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;

}

.left-section{
    display: flex;
    align-items: center;
}

.middle-section {
    flex: 1;
    margin-left: 70px;
    margin-right: 35px;
    max-width: 500px;
    display: flex;
    align-items: center;
}

.right-section{
    width: 180px;
    margin-right: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
}

.upload-icon{
    height: 24px;
}

.apps-icon{
    height: 24px;
}

.notif-icon{
    height: 24px;
}

.curr-user-pic{
    height: 32px;
    border-radius: 16px;
}

.hamburger-menu{
    height: 24px;
    margin-left: 24px;
    margin-right: 20px;
}

.youtube-logo{
    height: 20px;
}

.search{
    flex: 1;
    height: 36px;
    padding-left: 10px;
    font-size: 16px;
    border-width: 1px;
    border-style: solid;
    border-color: gray;
    border-radius: 2px;
    box-shadow: inset 1px 2px 3px 
    rgba(0, 0, 0, 0.05);
    width: 0;
}

.search::placeholder{
    font-size: 16px;
}

.search-button{
    height: 40px;
    width: 66px;
    background-color: rgb(240, 240, 240);
    border-width: 1px;
    border-style: solid;
    border-color: gray;
    margin-left: -1px;
    margin-right: 10px;
}

.search-icon{
    height: 25px;
    margin-top: 4px;
}

.voice-button{
    height: 40px;
    width: 40px;
    border-radius: 20px;
    border: none;
    background-color: rgb(245, 245, 245);
}

.voice-icon{
    height: 24px;
    margin-top: 4px;
}

